import infoBox from "../../components/boxstyle/infobox.vue"
import btn from "../../components/boxstyle/buttonstyle.vue"
import messBox from '../../components/message.vue'
import uploadMode from '../../components/uploadMode.vue'

export default {
	data() {
		return {
			// 留言模块接收值
			message: '',
			// 上传图片模块
			imgPathList: [],
			// 获取运单id值
			id: 0,
			longitude: null,
			latitude: null
		}
	},

	methods: {
		getMessage(data) {
			console.log(data)
		},

		imgPath(data) {
			this.imgPathList = data
			console.log(data)
		},

		async submitMessage() {
			uni.showLoading({});
			const opts = {
				url: "/personal/driver/modArriveImg",
				method: "post"
			}
			const param = {
				id: Number(this.id),
				imgs: this.imgPathList,
				longitude: this.longitude,
				latitude: this.latitude
			}
			console.log(param)

			if (this.imgPathList.length !== 0) {
				const res = await this.$http.httpTokenRequest(opts, param);
				console.log(res);
				if (res.data.code === 0) {
					uni.hideLoading({});
					uni.navigateBack({
						delta: 1
					})
				}
			} else {
				setTimeout(() => {
					uni.hideLoading({});
					uni.showModal({
						content: "请至少上传一张图片"
					})
				},700)
			}


		},

		getLocation() {
			var that = this;
			uni.getLocation({
				type: 'wgs84',
				success: function(res) {
					console.log('当前位置的经度：' + res.longitude)
					that.longitude = res.longitude;
					console.log('当前位置的纬度：' + res.latitude);
					that.latitude = res.latitude;
				}
			})
		}
	},

	onShow() {
		this.getLocation()
	},

	onLoad(options) {
		this.id = options.id
	},
	components: {
		infoBox,
		btn,
		messBox,
		uploadMode
	}
}
